<<template>
     <div class="mt-5">
    <v-card class="mx-auto" max-width="800">
      <v-card-title>Sesion</v-card-title>

      <v-card-text class="text--primary">
        <v-form ref="form" v-model="valid" lazy-validation>
          <v-container fluid>
            <v-row>
                 <v-col cols="12">
                    <v-text-field v-model="email" :rules="emailRules" label="E-mail" required outlined clearable></v-text-field>
                </v-col>
                <v-col cols="12" md="6">
                  <v-text-field
                    v-model="txtNombre"
                    :rules="txtNombreReglas"
                    label="Nombre"
                    required
                    outlined
                    clearable
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="6">
                  <v-text-field
                    v-model="numTelefono"
                    :rules="numTelefonoReglas"
                    label="Telefono"
                    required
                    outlined
                    clearable
                  ></v-text-field>
                </v-col>
              
              <v-col cols="12" sm="6">
                <v-text-field
                  :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                  :rules="[rules.required, rules.min]"
                  :type="show1 ? 'text' : 'password'"
                  name="input-10-2"
                  v-model="pass1"
                  label="Contraseña"
                  class="input-group--focused"
                  outlined
                  @click:append="show1 = !show1"
                  clearable
                ></v-text-field>
              </v-col>
               <v-col cols="12" sm="6">
                <v-text-field
                  :append-icon="show2 ? 'mdi-eye' : 'mdi-eye-off'"
                  :rules="[rules.required, rules.min]"
                  :type="show2 ? 'text' : 'password'"
                  name="input-10-2"
                  v-model="pass2"
                  label="Verificacion Contraseña"
                  class="input-group--focused"
                  outlined
                  @click:append="show2 = !show2"
                  clearable
                ></v-text-field>
              </v-col>
            </v-row>
          </v-container>

          <v-btn :disabled="!valid" color="success" class="mr-4" @click="registrar">Validate</v-btn>

          <v-btn color="error" class="mr-4" @click="reset">Reset Form</v-btn>
        </v-form>
      </v-card-text>
    </v-card>
  </div>
</template>
<script>
import { mapActions, mapState } from "vuex";
export default {
  data: () => ({
    valid: true,
    txtNombre: "",
    txtNombreReglas: [
      (v) => !!v || "Nombre es requerido",
      //   (v) => v.length <= 10 || "Name must be less than 10 characters",
    ],
    numTelefono: "",
    numTelefonoReglas: [
      (v) => !!v || "Telefono es requerido",
      //   (v) => v.length <= 10 || "Name must be less than 10 characters",
    ],
    email: "",
    emailRules: [
      (v) => !!v || "E-mail es requerido",
      (v) => /.+@.+\..+/.test(v) || "E-mail no valido",
    ],

    show1: false,
    password: "Password1",
    pass1: "",
    rules: {
      required: (value) => !!value || "Contraseña requerida.",
      min: (v) => v.length >= 8 || "Minimo 8 caracteres",
      emailMatch: () => "The email and password you entered don't match",
    },
    show2: false,
    password: "Password2",
    pass2: "",
    rules: {
      required: (value) => !!value || "verificacion de contraseña requerida.",
      min: (v) => v.length >= 8 || "Minimo 8 caracteres",
      emailMatch: () => "The email and password you entered don't match",
    },
  }),

  methods: {
    ...mapActions(["resgistrarCuenta"]),
    registrar() {
      if (this.$refs.form.validate()) {
        this.resgistrarCuenta({
          nombre: this.nombre,
          telefono: this.numTelefono,
          email: this.email,
          pass: this.pass1,
        });
      } else {
        alert(2);
      }
    },
    reset() {
      this.$refs.form.reset();
    },
  },
  computed: {
    ...mapState(["user"]),
  },
};
</script>